"use client";

import { cn } from "@/lib/utils";
import { AnimatedList } from "@/components/magicui/animated-list";
import {
  Stethoscope,
  Home,
  BookOpen,
  Plane,
} from "lucide-react";

interface BenefitItem {
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  time: string;
}

let benefits: BenefitItem[] = [
  {
    name: "Health Insurance",
    description:
      "Comprehensive medical, dental, and vision coverage for employees and their families, ensuring peace of mind and wellness support.",
    time: "Included",
    icon: <Stethoscope className="w-6 h-6 text-white" />,
    color: "#00C9A7",
  },
  {
    name: "Remote Work Flexibility",
    description:
      "Enjoy the freedom to work from home or any location globally, promoting work-life balance and reducing commute stress.",
    time: "Available",
    icon: <Home className="w-6 h-6 text-white" />,
    color: "#FFB800",
  },
  {
    name: "Learning & Development",
    description:
      "Annual budget for courses, certifications, and conferences to support your professional growth and continuous learning journey.",
    time: "$1,000/yr",
    icon: <BookOpen className="w-6 h-6 text-white" />,
    color: "#FF3D71",
  },
  {
    name: "Generous Paid Time Off",
    description:
      "Take advantage of 20+ days of paid vacation, sick leave, and personal days to rest, recharge, and spend time with loved ones.",
    time: "20+ Days",
    icon: <Plane className="w-6 h-6 text-white" />,
    color: "#1E86FF",
  },
];

benefits = Array.from({ length: 10 }, () => benefits).flat();

const BenefitCard = ({
  name,
  description,
  icon,
  color,
  time,
}: BenefitItem) => {
  return (
    <figure
      className={cn(
        "relative mx-auto min-h-fit w-full max-w-[600px] cursor-pointer overflow-hidden rounded-2xl p-5",
        "transition-all duration-200 ease-in-out hover:scale-[103%]",
        "bg-white [box-shadow:0_0_0_1px_rgba(0,0,0,.03),0_2px_4px_rgba(0,0,0,.05),0_12px_24px_rgba(0,0,0,.05)]",
        "transform-gpu dark:bg-transparent dark:backdrop-blur-md dark:[border:1px_solid_rgba(255,255,255,.1)] dark:[box-shadow:0_-20px_80px_-20px_#ffffff1f_inset]"
      )}
    >
      <div className="flex flex-row items-start gap-4">
        <div
          className="flex h-12 w-12 items-center justify-center rounded-2xl"
          style={{ backgroundColor: color }}
        >
          {icon}
        </div>
        <div className="flex flex-col overflow-hidden">
          <figcaption className="flex flex-row items-center whitespace-pre-wrap text-lg font-semibold dark:text-white">
            <span>{name}</span>
            <span className="mx-2">Â·</span>
            <span className="text-sm text-gray-500">{time}</span>
          </figcaption>
          <p className="mt-1 text-sm font-normal text-gray-700 dark:text-white/70">
            {description}
          </p>
        </div>
      </div>
    </figure>
  );
};

export function CareerBenefits({ className }: { className?: string }) {
  return (
    <div
      className={cn(
        "relative flex h-[500px] w-full flex-col overflow-hidden p-2",
        className
      )}
    >
      <AnimatedList>
        {benefits.map((item, idx) => (
          <BenefitCard {...item} key={idx} />
        ))}
      </AnimatedList>

      <div className="pointer-events-none absolute inset-x-0 bottom-0 h-1/4 bg-gradient-to-t from-background"></div>
    </div>
  );
}
